<template>
  <div class="admin-panel-container">
    <h1>Admin Panel Container!</h1>
  </div>
</template>

<script>
export default {
  data() {
    return {};
  },
  methods: {
    auth(u, p) {
      this.$http
        .post("/auth", {
          username: u,
          password: p,
        })
        .then((response) => {
          if (!response.data[0]) {
            this.$router.push("/404");
          }
        });
    },
  },
  mounted() {
    let userInfo = Object.assign(
      {},
      JSON.parse(localStorage.getItem("userInfo"))
    );
    console.log(userInfo);
    if (Object.keys(userInfo).length == 0) {
      console.log("here");
      this.$router.push("/404");
    }
    console.log(userInfo);
    this.auth(userInfo.username, userInfo.password);
  },
};
</script>

<style lang="scss" scoped>
.admin-panel-container {
  padding: 0;
}
</style>
